"use strict";

require("../docs.css");

require("@ebay/skin/global");

require("@ebay/skin/utility");

require("@ebay/skin/button");

require("@ebay/skin/alert-dialog");

require("@ebay/skin/confirm-dialog");

require("@ebay/skin/drawer-dialog");

require("@ebay/skin/fullscreen-dialog");

require("@ebay/skin/icon-button");

require("@ebay/skin/lightbox-dialog");

require("@ebay/skin/link");

require("@ebay/skin/panel-dialog");

require("@ebay/skin/snackbar-dialog");

require("@ebay/skin/textbox");

require("@ebay/skin/toast-dialog");

var DialogButton = require('../../packages/makeup-dialog-button');

var LightboxDialog = require('../../packages/makeup-lightbox-dialog');

var AlertDialog = require('../../packages/makeup-alert-dialog');

var ConfirmDialog = require('../../packages/makeup-confirm-dialog');

var DrawerDialog = require('../../packages/makeup-drawer-dialog');

var FullscreenDialog = require('../../packages/makeup-fullscreen-dialog');

var InputDialog = require('../../packages/makeup-input-dialog');

var PanelDialog = require('../../packages/makeup-panel-dialog');

var SnackbarDialog = require('../../packages/makeup-snackbar-dialog');

var ToastDialog = require('../../packages/makeup-toast-dialog');

var log = function log(e) {
  return console.log(e);
}; // eslint-disable-line no-console


window.onload = function () {
  document.querySelectorAll('.dialog-button').forEach(function (el, i) {
    var dialogId = el.dataset.makeupFor;
    var dialogEl = document.getElementById(dialogId);
    var dialogClassList = dialogEl.classList;
    var dialogWidget;

    if (dialogClassList.contains('confirm-dialog')) {
      dialogWidget = new ConfirmDialog(dialogEl);
    } else if (dialogClassList.contains('alert-dialog')) {
      dialogWidget = new AlertDialog(dialogEl);
    } else if (dialogClassList.contains('lightbox-dialog--input')) {
      dialogWidget = new InputDialog(dialogEl);
    } else if (dialogClassList.contains('fullscreen-dialog')) {
      dialogWidget = new FullscreenDialog(dialogEl);
    } else if (dialogClassList.contains('snackbar-dialog')) {
      dialogWidget = new SnackbarDialog(dialogEl);
    } else if (dialogClassList.contains('toast-dialog')) {
      dialogWidget = new ToastDialog(dialogEl);
    } else if (dialogClassList.contains('drawer-dialog')) {
      dialogWidget = new DrawerDialog(dialogEl);
    } else if (dialogClassList.contains('panel-dialog')) {
      dialogWidget = new PanelDialog(dialogEl);
    } else if (dialogClassList.contains('lightbox-dialog')) {
      dialogWidget = new LightboxDialog(dialogEl);
    }

    var buttonWidget = new DialogButton(el, dialogWidget);

    dialogWidget._el.addEventListener('dialog-open', log);

    dialogWidget._el.addEventListener('dialog-close', log);
  });
};